<template>
    <yd-layout>
        <yd-navbar title="未接来电" bgcolor="#70b6ef" color="#fff" fontsize="0.36rem">
            <router-link to="/" slot="left">
                <yd-navbar-back-icon color="#fff">
                    <slot color="#fff">返回</slot>
                </yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
        <div class="missMain" v-if="myData == ''">
            <div class="missInfo">
                <yd-flexbox>
                    <yd-flexbox-item>暂无数据</yd-flexbox-item>
                </yd-flexbox>
            </div>
        </div>
        <div class="missMain" v-for="item in myData" v-else>
            <div class="missTime" v-text="item.calltime.split(' ')[0]">
            </div>
            <div class="missInfo">
                <yd-flexbox>
                    <div class="grayWord">来电人：</div>
                    <yd-flexbox-item v-text="item.callername ==undefined? item.callernumber: item.callername"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <div class="grayWord">职务：</div>
                    <yd-flexbox-item v-text="item.callerremark"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <div class="grayWord">来电时间：</div>
                    <yd-flexbox-item v-text="item.calltime"></yd-flexbox-item>
                </yd-flexbox>
                <a :href="'tel:'+item.callernumber"><yd-flexbox>
                    <yd-flexbox-item>点击回拨</yd-flexbox-item>
                    <yd-navbar-next-icon></yd-navbar-next-icon>
                </yd-flexbox></a>
            </div>
        </div>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'missedCall',
    data() {
        return {
            myData: []
        }
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    mounted: function() {
        this.pageRender();
        document.setTitle('未接来电');
    },
    methods: {
        pageRender() {
            let vm = this;
            vm.get(vm.conf.missedCall + '/getMissedCall', {
                'ssotoken': vm.getUserInfo.empcode
            }, function(res) {
                vm.myData = res.jsonData.reverse();
            });
        }
    }
}
</script>
<style scoped>
.missMain {
    padding: 0 0.13rem;
    background-color: #fff;
}

.missMain2 {
    margin-top: 0.2rem;
}

.missMain .yd-flexbox {
    font-size: 0.28rem;
    border-bottom: 1px solid #ccc;
    line-height: 0.8rem;
    padding-left: 0.2rem;
    color: #333;
}

.missTime {
    font-size: 0.28rem;
    border-bottom: 1px solid #ccc;
    line-height: 0.8rem;
    padding-left: 0.2rem;
    color: #333;
}

.grayWord {
    color: #949494;
}

.missMain .yd-flexbox:last-child {
    font-weight: bold;
    font-size: 0.3rem;
    border-bottom: none;
}

.missInfo {
    margin-bottom: 0.2rem;
}
</style>


